@font-face {
	font-family: 'LAB디지털';
	src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/LAB디지털.woff')
		format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'ONE-Mobile-Title';
	src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-Title.woff')
		format('woff');
	font-weight: normal;
	font-style: normal;
}

.wrap {
	text-align: center;
}

#title {
	font-size: 25px;
	color: #252525;
	font-family: 'ONE-Mobile-Title';
}

form {
	margin: 0 auto;
	padding: 20px;
	width: 80%;
	text-align: center;
}
.flex-container {
	display: flex;
	justify-content: center;
	flex-direction: row;
}

.input {
	margin: 5px;
	border-style: solid;
	border-width: 0 0 1px;
	border-radius: 3px;
	border-color: #777777;
}

.input:focus {
	border-style: solid;
	border-color: #252525;
}

.flex-container-right {
	margin-top: 20px;
	display: flex;
	flex-direction: row;
	justify-content: end;
}

.flex-container-left {
	margin: 30px auto 0 auto;
	padding: 0 25px;
	width: 80%;
	display: flex;
	justify-content: start;
}

.span-container {
	padding-right: 20px;
	text-align: center;
	display: flex;
	justify-content: center;
	flex-direction: column;
}

#reply {
	font-family: Arial, Helvetica, sans-serif;
}

.button {
	margin-right: 0;
	width: 100px;
	cursor: pointer;
	font-size: 20px;
	font-family: 'LAB디지털';
	border-style: solid;
	border-width: 0 0 1px;
	border-radius: 3px;
	border-color: #777777;
	transition: all 0.1s ease-in-out;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.06), 0 1px 1px 0 rgba(0, 0, 0, 0.07);
}

.button:hover {
	transform: scale(1.05);
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1), 0 1px 1px 0 rgba(0, 0, 0, 0.09);
}

input {
	font-family: Arial, Helvetica, sans-serif;
	padding: 5px 10px;
	width: 100%;
	height: 30px;
	outline: none;
}

textarea {
	font-family: Arial, Helvetica, sans-serif;
	padding: 10px 10px;
	width: 100%;
	height: 200px;
	min-height: 100px;
	resize: none;
	outline: none;
}
